import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import styles from '../../FilmListltem.module.css';
import { ReactComponent as Busket } from '../../busket.svg';

const FavoriteFilm = ({ film, deleteFromFavorites }) => {
  return (
    <div>
      <h3> {film.title} </h3>{' '}
      <section className={styles.filmItemWrapper}>
        <img
          className={styles.img}
          src={
            film.poster_path
              ? `https://image.tmdb.org/t/p/w200/${film.poster_path}`
              : 'https://www.viser.edu.rs/images/no_photo.jpeg'
          }
          alt="film poster"
        />
        <button
          type="button"
          title="delete from list"
          onClick={() => deleteFromFavorites(film.id)}
          className={styles.button}
        >
          <Busket className={styles.busketSvg} />{' '}
        </button>{' '}
        <NavLink to={`/${film.title}/${film.id}`} className={styles.link}>
          <button type="button" title="show more" className={styles.button}>
            ...
          </button>{' '}
        </NavLink>{' '}
      </section>{' '}
    </div>
  );
};
FavoriteFilm.propTypes = {
  film: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    poster_path: PropTypes.string.isRequired,
  }).isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
};
export default FavoriteFilm;
